"use client";

import AdminAvatar from "@/app/components/adminAvatar";
import React, { useState } from "react";

export default function Sidebar() {
  const [isDocumentsOpen, setIsDocumentsOpen] = useState(false);
  const [isOrganizationOpen, setIsOrganizationOpen] = useState(false);
  const [isAdminDocumentsOpen, setIsAdminDocumentsOpen] = useState(false);

  return (
    <div className="bg-gray-900 text-white max-w-full min-h-screen flex">
      <div className="bg-gray-800 w-64 h-screen p-4 flex justify-between align-center flex-col">
        <AdminAvatar></AdminAvatar>
        <div className="flex-1">
          <button className="w-full text-left mb-4">+ Новый документ</button>

          <div className="mb-4">
            <button
              onClick={() => setIsDocumentsOpen(!isDocumentsOpen)}
              className="w-full text-left mb-2"
            >
              Документы
            </button>
            {isDocumentsOpen && (
              <ul className="ml-4 text-sm text-gray-400">
                <li>
                  Входящие <span className="float-right">3</span>
                </li>
                <li>
                  Исходящие <span className="float-right">4</span>
                </li>
                <li>
                  Подписанные <span className="float-right">5</span>
                </li>
                <li>
                  Корзина <span className="float-right">0</span>
                </li>
              </ul>
            )}
          </div>

          <div className="mb-4">
            <button
              onClick={() => setIsOrganizationOpen(!isOrganizationOpen)}
              className="w-full text-left mb-2"
            >
              Организация
            </button>
            {isOrganizationOpen && (
              <ul className="ml-4 text-sm text-gray-400">
                <li>Сотрудники</li>
              </ul>
            )}
          </div>

          <div className="mb-4">
            <button
              onClick={() => setIsAdminDocumentsOpen(!isAdminDocumentsOpen)}
              className="w-full text-left mb-2"
            >
              Документы
            </button>
            {isAdminDocumentsOpen && (
              <ul className="ml-4 text-sm text-gray-400">
                <li>Виды орган-й</li>
                <li>Виды док-в</li>
                <li>Маршруты</li>
              </ul>
            )}
          </div>

          <button className="w-full text-left">Администрирование</button>
        </div>
      </div>

      <div className="flex-1 flex items-center justify-center">
        <div className="text-center">
          <h1 className="text-4xl mb-4">DDM</h1>
          <p className="text-lg text-gray-400">Digital document management</p>
        </div>
      </div>
    </div>
  );
}
